import React from "react";
import Container from "react-bootstrap/Container";
import Typist from 'react-typist-component';
import { Jumbotron } from "./migration";

import AboutMe from "./AboutMe";
import Project from "./Project";
import Skills from "./Skills";
import Experience from "./Experience";
import EducationPage from "./EducationPage"; // Keep it as a section

import {
  mainBody,
  about,
  repos,
  skills,
  experiences,
  education
} from "../../editable-stuff/config";

const MainBody = React.forwardRef((props, ref) => {
  return (
    <Jumbotron
      fluid
      id="home"
      style={{
        background: `linear-gradient(136deg,${props.gradient})`,
        backgroundSize: "1200% 1200%",
      }}
      className="title bg-transparent bgstyle text-light min-vh-100 d-flex align-content-center align-items-center flex-wrap m-0"
    >
      <div id="stars"></div>
      <Container className="text-center">
        <h1 ref={ref} className="display-1">
          {props.title}
        </h1>
        <Typist>
          <div className="lead typist">
            {props.message}
          </div>
        </Typist>

        {/*  Projects Section */}
        {repos.show && (
          <div id="projects">
            <Project
              heading={repos.heading}
              username={repos.gitHubUsername}
              length={repos.reposLength}
              specfic={repos.specificRepos}
            />
          </div>
        )}

        {/*  About Section */}
        {about.show && (
          <div id="about">
            <AboutMe
              heading={about.heading}
              message={about.message}
              link={about.imageLink}
              imgSize={about.imageSize}
              resume={about.resume}
            />
          </div>
        )}

        {/*  Experience Section */}
        {experiences.show && (
          <div id="experience">
            <Experience experiences={experiences} />
          </div>
        )}

        {/*  Skills Section */}
        {skills.show && (
          <div id="skills">
            <Skills
              heading={skills.heading}
              hardSkills={skills.hardSkills}
              softSkills={skills.softSkills}
            />
          </div>
        )}

        {/*  Education Section */}
        {education.show && (
          <div id="education">
            <EducationPage data={education} />
          </div>
        )}
      </Container>
    </Jumbotron>
  );
});

export default MainBody;
