import React, { useState } from "react";
import { useScrollPosition } from "../hooks/useScrollPosition";
import useResizeObserver from "../hooks/useResizeObserver";
import Navbar from "react-bootstrap/Navbar";
import Nav from "react-bootstrap/Nav";
import { mainBody, repos, about, skills } from "../editable-stuff/config.js";
import { Link } from "react-scroll";

import { education } from "../editable-stuff/config.js";


const Navigation = React.forwardRef((props, ref) => {
  const [isTop, setIsTop] = useState(true);
  const [scrollPosition, setScrollPosition] = useState(0);
  const navbarMenuRef = React.useRef();
  const navbarDimensions = useResizeObserver(navbarMenuRef);
  const navBottom = navbarDimensions ? navbarDimensions.bottom : 0;

  useScrollPosition(
    ({ prevPos, currPos }) => {
      if (!navbarDimensions) return;
      currPos.y + ref.current.offsetTop - navbarDimensions.bottom > 5
        ? setIsTop(true)
        : setIsTop(false);
      setScrollPosition(currPos.y);
    },
    [navBottom]
  );

  React.useEffect(() => {
    if (!navbarDimensions) return;
    navBottom - scrollPosition >= ref.current.offsetTop
      ? setIsTop(false)
      : setIsTop(true);
  }, [navBottom, navbarDimensions, ref, scrollPosition]);

  return (
    <Navbar
      ref={navbarMenuRef}
      className={`px-3 fixed-top ${!isTop ? "navbar-white" : "navbar-transparent"}`}
      expand="lg"
    >
      <Navbar.Brand className="navbar-brand" as={Link} to="home" smooth={true} duration={500}>
        {`<${mainBody.lastName} />`}
      </Navbar.Brand>
      <Navbar.Toggle aria-controls="basic-navbar-nav" className="toggler" />
      <Navbar.Collapse id="basic-navbar-nav">
        <Nav className="navbar-nav mr-auto">

          {/*  Scroll to Projects */}
          {repos.show && (
            <Nav.Link as={Link} to="projects" smooth={true} duration={500}>
              Projects
            </Nav.Link>
          )}

          {/*  Scroll to About */}
          {about.show && (
            <Nav.Link as={Link} to="about" smooth={true} duration={500}>
              About
            </Nav.Link>
          )}

          {/*  Scroll to Skills */}
          {skills.show && (
            <Nav.Link as={Link} to="skills" smooth={true} duration={500}>
              Skills
            </Nav.Link>
          )}

          {/*  Scroll to Education */}
          {education.show && (
            <Nav.Link as={Link} to="education" smooth={true} duration={500}>
              Education
            </Nav.Link>
          )}
        </Nav>
      </Navbar.Collapse>
    </Navbar>
  );
});

export default Navigation;
